import { observable, action, makeObservable } from 'mobx';

class AppStore {
    page = '';
    constructor() {
        makeObservable(this, {
            page: observable,
            changePage: action
        });
    }
    changePage(value) {
        this.page = value;
        console.log(this.page);
    }
}

export default new AppStore();
